import Modal from "react-bootstrap/Modal"

export default function PresentationModal({
    show,
    setShow
}) {

    const handleClose = () => setShow(false)

    return (
        <Modal
            id="presModal"
            show={show}
            backdrop="static"
            keyboard={false}
            backdropClassName="presentationModalBackdrop"
            size="lg"
        >
            <Modal.Body>
                <h3>
                    GALERA ARTSTICA VIRTUAL
                </h3>
                <h4>
                    De Yuyi Flores
                </h4>
                <p>
                    Una maana, una tarde o una noche tal vez,
                    paseabas por un pueblo, por una ciudad o por algn lugar del mundo
                    y viste una galera artstica. Te detuviste, entraste, miraste, slo por el gusto de mirar,
                    tal vez comprar algo, preguntar o simplemente admirar lo que en ella hay.
                    Hoy te invito a un paseo muy especial. Es el paseo por una pgina web de una
                    galera artstica virtual. Una pgina que est en construccin y estar en
                    permanente cambio: diferentes obras, diferentes propuestas, diferentes artistas
                    e invitados. En definitiva, un arte activo y en movimiento.
                </p>
                <p>
                    Pero recuerda:
                    el valor de un objeto no es el objeto,
                    el valor de un relato no es el relato,
                    el valor de una obra de arte no es la obra en s.
                </p>
                <p>
                    El valor est en el tiempo.
                    El tiempo que alguien us para hacer cada cosa de esta galera tan especial.
                    Ese fue mi tiempo, querido amigo, querida amiga.
                    El tiempo de tantos aos de trabajo.
                    Un tiempo de disfrute,
                    un tiempo de msica, canto, oracin y encuentros,
                    un tiempo en soledad, un tiempo en compaa,
                    un tiempo de hacer y de no hacer.
                    Tanto tiempo, y tan poco a la vez!
                </p>
                <p>
                    Pero tambin el valor lo da el espectador que valida la obra artstica.
                    Es tu tiempo, el que dedicas al ver esta galera.
                    Por todo esto, disfruta, admira, compra, pregunta o solo mira.
                    Porque ese tiempo se suma al mo.
                </p>
                <p>
                    Gracias!
                </p>
                <p className="cursor-pointer clickable" onClick={handleClose}>
                    Continuar al sitio {'>>'}
                </p>
            </Modal.Body>
        </Modal>
    )
}